      </el-table-column>
      <el-table-column label="当前连接人数" prop="address">
        <template slot="header">
          <span style="color:#363636;">当前连接人数</span>
        </template>
        <template slot-scope="scope">
          <div class="congestion">
            <div class="status">
              <div v-if="scope.row.congestion === 'error'">获取状态失败</div>
              <div class="status-bar" v-else :class="scope.row.congestion"></div>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="操作" prop="address" width="150">
        <template slot="header">
          <span style="color:#363636;">操作</span>
        </template>
        <template slot-scope="scope">
          <div>
            <el-button
                v-if="(extInfo.connection && (scope.row.ip === extInfo.connection.ip))" type="danger"
                @click="disconnect"
            >断开</el-button>
            <el-button
                v-else-if="scope.row.free || isShared"
                type="success"
                @click="connect(scope.row)"
                :disabled="(scope.row.congestion === 'full' || scope.row.congestion === null || scope.row.congestion === 'error')"
            >
              连接
            </el-button>
            <el-button v-else type="warning" @click="shareQQWeibo">立即升级</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <p class="topText" style="margin-top:20px">提示</p>
    <p class="topText" style="padding:5px 40px">1.网络节点宽带有限，为满足大部分人的间隔需求，每次连接有效期 30 分钟、过期连接自动断开。</p>
    <p class="topText" style="padding:5px 40px">2.请大家自觉使用，避免产生大量视频类流量，导致他人正常研发受影响。</p>
    <p class="topText">联系方式:</p>
    <P class="topText" style="color:#363636;margin-bottom:50px"><a
        href="https://github.com/pablocc1979/Green-Hub-Proxy/issues" target="_blank">
      https://github.com/pablocc1979/Green-Hub-Proxy/issues </a></P>
    <div :style="popupDivShow ? 'display:block' : 'display:none'" class="PopupsDiv">
      <div
          style="width:485px;height:384px;background:white;box-shadow: 0px 10px 10px #ccc;margin:0 auto;position: relative;border-radius: 15px;overflow: hidden;">
        <p class="CloseIcon" @click="closeDiv"><i class="el-icon-close"></i></p>
        <p style="width:100%;height:202px;background:#F0C537;text-align:center">
          <img alt="" height="137" src="../assets/popus.png" style="margin-top:30px" width="131">
        </p>
        <p v-if="shareType !== 4" class="text" style=" margin-top: 18px;"> 喜欢 GreenHub 😊</p>
        <p v-if="shareType === 4" class="text" style=" margin-top: 18px;"> 感谢分享! 😊</p>
        <p v-if="shareType !== 4" class="text"> 完成任意分享， 本月<b>免费无限使用</b> ！</p>
        <p v-if="shareType === 4" class="text"> 恭喜你获得本月 <b>免费无限使用</b> 权限！</p>
        <p style=" margin-top: 28px; display:flex;justify-content: center;">
          <button :disabled='shareType === 2' :style="shareType === 2 ? 'background:#C1C1C1' : ''" class="shareButton"
                  @click="shareQQ">
            <svg class="svg-icon" height="22" style="fill: rgb(255, 255, 255);margin-right:5px;margin-top:-2px" viewBox="64 64 896 896" width="22"
                 xmlns="http://www.w3.org/2000/svg">
              <path
                  d="M824.8 613.2c-16-51.4-34.4-94.6-62.7-165.3C766.5 262.2 689.3 112 511.5 112 331.7 112 256.2 265.2 261 447.9c-28.4 70.8-46.7 113.7-62.7 165.3-34 109.5-23 154.8-14.6 155.8 18 2.2 70.1-82.4 70.1-82.4 0 49 25.2 112.9 79.8 159-26.4 8.1-85.7 29.9-71.6 53.8 11.4 19.3 196.2 12.3 249.5 6.3 53.3 6 238.1 13 249.5-6.3 14.1-23.8-45.3-45.7-71.6-53.8 54.6-46.2 79.8-110.1 79.8-159 0 0 52.1 84.6 70.1 82.4 8.5-1.1 19.5-46.4-14.5-155.8z"></path>
            </svg>
            <span v-if="shareType !== 2">分享到QQ空间</span>
            <span v-if="shareType === 2">分享中…</span>
          </button>
          <button :disabled='shareType === 3' :style="shareType === 3 ? 'background:#C1C1C1' : ''" class="shareButton"
                  @click="shareToXl">
            <svg class="svg-icon" height="24" style="fill: rgb(255, 255, 255);margin-right:5px;" viewBox="0 0 512 512" width="24"
                 xmlns="http://www.w3.org/2000/svg">
              <path
                  d="M407 177.6c7.6-24-13.4-46.8-37.4-41.7-22 4.8-28.8-28.1-7.1-32.8 50.1-10.9 92.3 37.1 76.5 84.8-6.8 21.2-38.8 10.8-32-10.3zM214.8 446.7C108.5 446.7 0 395.3 0 310.4c0-44.3 28-95.4 76.3-143.7C176 67 279.5 65.8 249.9 161c-4 13.1 12.3 5.7 12.3 6 79.5-33.6 140.5-16.8 114 51.4-3.7 9.4 1.1 10.9 8.3 13.1 135.7 42.3 34.8 215.2-169.7 215.2zm143.7-146.3c-5.4-55.7-78.5-94-163.4-85.7-84.8 8.6-148.8 60.3-143.4 116s78.5 94 163.4 85.7c84.8-8.6 148.8-60.3 143.4-116zM347.9 35.1c-25.9 5.6-16.8 43.7 8.3 38.3 72.3-15.2 134.8 52.8 111.7 124-7.4 24.2 29.1 37 37.4 12 31.9-99.8-55.1-195.9-157.4-174.3zm-78.5 311c-17.1 38.8-66.8 60-109.1 46.3-40.8-13.1-58-53.4-40.3-89.7 17.7-35.4 63.1-55.4 103.4-45.1 42 10.8 63.1 50.2 46 88.5zm-86.3-30c-12.9-5.4-30 .3-38 12.9-8.3 12.9-4.3 28 8.6 34 13.1 6 30.8.3 39.1-12.9 8-13.1 3.7-28.3-9.7-34zm32.6-13.4c-5.1-1.7-11.4.6-14.3 5.4-2.9 5.1-1.4 10.6 3.7 12.9 5.1 2 11.7-.3 14.6-5.4 2.8-5.2 1.1-10.9-4-12.9z"></path>
            </svg>
            <span v-if="shareType !== 3">分享到微博</span>
            <span v-if="shareType === 3">分享中…</span>
          </button>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Vue from 'vue'

export default {
  name: 'App',
  components: {},
  data() {
    return {
      // 插件基本信息
      extInfo: {
        version: '0.9',
        uid: '',
        connection: null,
        sharedDate: null,
        popupTime: null
      },
      // 最新版本
      remoteVersion: '',
      // 总连接人数
      totalConnections: '...',
      popupDivShow: false,
      shareType: 1, //1是没有分享的状态 2是qq分享中 3是微博分享中 4是分享完成
      proxies: [],
      proxiesLoading: true,
      // 辅助变量
      today: new Date()
    }
  },
  methods: {
    // 获取插件基本信息
    async getExtInfo() {
      let resp = await browser.runtime.sendMessage({
        action: 'extInfo',
        data: {}
      })
      this.extInfo = {
        ...this.extInfo,
        ...resp
      }
      this.extInfo.sharedDate = this.extInfo.sharedDate === null ? null : new Date(this.extInfo.sharedDate)
      this.extInfo.popupTime = this.extInfo.popupTime === null ? null : new Date(this.extInfo.popupTime)
      console.debug('getExtInfo: getting basic information: ', this.extInfo)
    },
    // 解析 html 特殊字符，用于解析 github 返回的字符串
    decodeHtml(html) {
      let txt = document.createElement("textarea")
      txt.innerHTML = html
      return txt.value
    },
    // 获取代理节点信息
    async requestProxiesUrl(url, token, anotherCancelToken, type) {
      try {
        console.debug('fetching url: ', url)
        const rst = await axios.get(url, {
          cancelToken: token,
          responseType: 'text'
        })
        console.debug('fetch ', type, ' status: ', rst)
        if (rst.status !== 200) {
          return
        }
        // 取消另一个请求
        anotherCancelToken.cancel()
        let data = {}
        switch (type) {
          case 'aws':
            data = rst.data
            break;
          case 'github':
            data = JSON.parse(this.decodeHtml(rst.data).match(/@@@@(.*)&&&&/)[1])
            break;
        }
        this.proxies = data.nodes ? data.nodes : []
        this.remoteVersion = data.version ? data.version : this.extInfo.version
      } catch (e) {
        if (axios.isCancel(e)) {
          console.debug('e is cancellation')
        }
        console.debug('error: ', e)
      }
    },
    async fetchProxies() {
      this.proxiesLoading = true
      console.debug('fetching proxies...')
      const proxiesUrl = 'https://static.greenhubex.tk/data.list'
      const proxiesUrlGithub = 'https://github.com/ghotst0xdeadbeef/algorithms/blob/master/linkedlist/data.list.bak'
      let cancelTokenAws = axios.CancelToken.source()
      let cancelTokenGithub = axios.CancelToken.source()
      await Promise.allSettled([
          this.requestProxiesUrl(proxiesUrl, cancelTokenAws.token, cancelTokenGithub, 'aws'),
          this.requestProxiesUrl(proxiesUrlGithub, cancelTokenGithub.token, cancelTokenAws, 'github'),
      ])
      this.proxiesLoading = false
    },
    // ip 地址脱敏
    ipSec(ipAddr) {
      let arr = ipAddr.split('.')
      arr[1] = arr[2] = '***'
      return arr.join('.')
    },
    // 获取拥挤程度
    async fetchStatus(proxy, i) {
      try {
        const rst = await axios.post(`${proxy.host}/status`, {
          f: this.extInfo.uid,
          v: this.extInfo.version
        })
        if (rst.status !== 200) {
          proxy.capacity = 0
          proxy.current = 1
        } else {
          proxy.capacity = rst.data.capacity
          proxy.current = rst.data.current
        }
      } catch (_) {
        proxy.capacity = 0
        proxy.current = 1
      }
      proxy.congestion = this.calCongestion(proxy)
      Vue.set(this.proxies, i, proxy)
      return proxy
    },
    // 计算拥挤程度
    // full, high, medium-high, medium-low, low
    calCongestion(proxy) {
      if (proxy.capacity <= 0) {
        console.debug('总容量小于 0')
        return 'error'
      }
      const level = parseInt((proxy.current / proxy.capacity * 10).toFixed(0))
      console.debug('congestion level: ', level)
      switch (level) {
        case 0:
        case 1:
          console.log('case 0')
          return 'low'
        case 2:
        case 3:
        case 4:
          return 'medium-low'
        case 5:
        case 6:
          return 'medium-high'
        case 7:
        case 8:
          return 'high'
        case 9:
        case 10:
        default:
          return 'full'
      }
    },
    // 检测更新
    checkUpdate(current, remote) {
      console.debug('checking updates...', current, remote)
      current = current.split('.')
      current = parseFloat([current[0], current.slice(1).join('')].join('.'))
      remote = remote.split('.')
      remote = parseFloat([remote[0], remote.slice(1).join('')].join('.'))
      return remote > current
    },
    // 进入页面后的启动函数
    async init() {
      console.debug('option page initiating...')
      // 获取当前插件基本信息，版本号，uid, 分享状态 等
      await this.getExtInfo()
      // 获取节点列表
      await this.fetchProxies()
      this.totalConnections = 0
      if (this.checkUpdate(this.extInfo.version, this.remoteVersion)) {
        console.log('<><><><><>><><><><><><><><><><> new version found')
        alert('新版本已发布，请点击确定下载新版本')
        window.open('https://github.com/pablocc1979/Green-Hub-Proxy')
        window.close()
      }
      // 统计各节点人数，并发送给 background 进行同步
      for (let i = 0; i < this.proxies.length; i++) {
        this.fetchStatus(this.proxies[i], i).then(rst => {
          this.totalConnections += rst.current
          browser.runtime.sendMessage({
            action: 'setTotalConnections',
            data: this.totalConnections
          })
        })
      }
    },
    // 检测弹出情况
    async checkShareStatus() {
      this.popupDivShow = !this.isShared && this.needPopup
    },
    // 连接代理
    async connect(proxy) {
      const resp = await browser.runtime.sendMessage({
        action: 'connect',
        data: proxy
      })
      if (!resp) {
        alert('链接失败，请稍后重试')
      } else {
        this.getExtInfo().then()
      }
      console.debug('connected: ', resp)
    },
    async disconnect() {
      await browser.runtime.sendMessage({
        action: 'disconnect',
        data: {}
      })
      this.init().then()
    },
    shareQQ() {
      this.shareType = 2;
      let title = '个人推荐：GreenHub 绿墙，五星好用的网络出海工具，500+五星好评。';
      let url = 'https://github.com/pablocc1979/Green-Hub-Proxy';
      let picurl = '../assets/picture1.png'
      let shareqqzonestring = 'http://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?summary=' + title + '&url=' + url + '&pics=' + picurl;
      window.open(shareqqzonestring, 'newwindow', 'height=400,width=400,top=100,left=100');
      let that = this
      setTimeout(function () {
        browser.runtime.sendMessage({
          action: 'userShared',
          data: {}
        }).then(() => {
          that.getExtInfo().then()
        })
        that.shareType = 4
      }, 5000)
    },
    shareToXl() {
      this.shareType = 3;
      let title = '个人推荐：GreenHub 绿墙，五星好用的网络出海工具，500+五星好评。';
      let url = 'https://github.com/pablocc1979/Green-Hub-Proxy';
      let picurl = '../assets/picture1.png'
      let sharesinastring = 'http://v.t.sina.com.cn/share/share.php?title=' + title + '&url=' + url + '&content=utf-8&sourceUrl=' + url + '&pic=' + picurl;
      window.open(sharesinastring, 'newwindow', 'height=400,width=400,top=100,left=100');
      let that = this
      setTimeout(function () {
        browser.runtime.sendMessage({
          action: 'userShared',
          data: {}
        }).then(() => {
          that.getExtInfo().then()
        })
        that.shareType = 4
      }, 5000)
    },
    shareQQWeibo() {
      this.popupDivShow = true
    },
    closeDiv() {
      this.popupDivShow = false
    },
    // 帮助函数
    sleep (ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    }
  },
  computed: {
    // 判断是否已分享（分享后当月有效）
    isShared() {
      console.debug('share status: ', this.extInfo.sharedDate)
      if (!this.extInfo.sharedDate) {
        return false
      }
      return this.today.getMonth() === this.extInfo.sharedDate.getMonth()
    },
    // 判断是否需要弹出分享弹窗
    needPopup() {
      if (!this.extInfo.popupTime) {
        return true
      }
      return this.today.getDate() !== this.extInfo.popupTime.getDate()
    }
  },
  watch: {
    // 当分享弹窗弹出过后，记录下来时间
    popupDivShow() {
      if (this.popupDivShow) {
        browser.runtime.sendMessage({
          action: 'sharePopupPopped',
          data: {}
        })
      }
    }
  },
  created() {
    this.init().then(async () => {
      await this.sleep(1000 * 60 * 3)
      await this.checkShareStatus()
    })
    browser.runtime.onMessage.addListener(msg => {
      if (typeof msg === 'object' && 'action' in msg) {
        switch (msg.action) {
          case 'disconnected':
            this.init().then()
            break
        }
      }
    })
  }
}
</script>

